import React from "react";
import styled from "styled-components";
import Sobre from "./Sobre";
import Portfolio from "./portfolio/Portfolio";
import Contactos from "./Contactos";

function Me(props) {
  const { setScroll, display } = props;

  const handleScroll = (event) => {
    let afterCasa = document.getElementById("afterCasa");
    let sobre = document.getElementById("sobre").scrollHeight;
    let portfolio = document.getElementById("portfolio").scrollHeight + sobre;
    /**/
    const scrollY = afterCasa.scrollTop;
    if (scrollY < sobre) {
      setScroll(2);
    } else if (scrollY >= sobre && scrollY < portfolio - 10) {
      setScroll(3);
    } else if (scrollY >= portfolio - 10) {
      setScroll(4);
    } /**/
  };

  return (
    <AfterCasa
      className="col-12 p-0"
      id="afterCasa"
      onScroll={handleScroll}
      display={display}
    >
      <Sobre />
      <Portfolio />
      <Contactos />
    </AfterCasa>
  );
}
/*height: calc(
      100vh - ${({ display }) => (display !== "none" ? "245px" : "66px")}
    ) !important;*/
const AfterCasa = styled.div`
  background-color: var(--mainBlackOpc);
  color: var(--mainWhite);
  p {
    color: var(--mainGray);
  }
  @media screen and (width: 768px) {
  }
  @media screen and (min-width: 768px) {
    height: calc(100vh - 65px);
    overflow-y: scroll;
    &::-webkit-scrollbar {
      max-width: 0.4rem !important;
    }

    &::-webkit-scrollbar-track {
      background: var(--mainGreen) !important;
    }

    &::-webkit-scrollbar-thumb {
      background-color: var(--mainBlack) !important;
    }
    /*firefox*/
    scrollbar-width: thin;
    scrollbar-color: var(--mainBlack) var(--mainGreen);
  }
  @media screen and (max-width: 767px) {
    overflow-y: hidden;
    min-height: calc(100vh);
  }
`;
export default Me;
