import React, { useState } from "react";
import styled from "styled-components";
import Button from "../Button";
import { Consumer } from "../../Context";

function Projectos(props) {
  const { id, name, info, link, src, mainTec, tec } = props;
  const [d, setD] = useState("none");
  const noD = () => {
    setD("none");
  };
  return (
    <Consumer>
      {({ setDisplayModal, setHandleModal, conteudo }) => {
        return (
          <Projecto
            className="col-12 col-lg-6 p-0"
            img={src}
            d={d}
            onMouseOut={() => {
              setD("block");
              setTimeout(noD, 1000 * 0.3);
            }}
          >
            <div className="inside d-none">
              <div id="top">
                <h4>{name}</h4>
                <p>{mainTec}</p>
              </div>
              <Button
                width="150px"
                id="bottom"
                onClick={() => {
                  setHandleModal({
                    id: id,
                    name: name,
                    info: info,
                    link: link,
                    src: src,
                    mainTec: mainTec,
                    tec: tec,
                  });
                  setDisplayModal("block");
                }}
              >
                {conteudo.me.portfolio.hover}
              </Button>
            </div>
          </Projecto>
        );
      }}
    </Consumer>
  );
}
const Projecto = styled.div`
  cursor: pointer;
  height: 300px;
  background-image: URL(${({ img }) => img});
  background-size: cover;
  background-position: center;
  text-align: center;
  p {
    color: var(--mainGreen) !important;
  }
  :hover {
    .inside {
      display: block !important;
      padding-top: 93px;
      /******************/
      -webkit-animation-name: top-bottom;
      -webkit-animation-duration: 0.3s;
      animation-name: top-bottom;
      animation-duration: 0.3s;
      #top {
        -webkit-animation-name: bottom-top;
        -webkit-animation-duration: 0.3s;
        animation-name: bottom-top;
        animation-duration: 0.3s;
      }
    }
  }
  .inside {
    width: 100%;
    height: 300px;
    background-color: var(--mainBlack) !important;
    display: ${({ d }) => d}!important;
    -webkit-animation-name: nohover-top-bottom;
    -webkit-animation-duration: 0.5s;
    animation-name: nohover-top-bottom;
    animation-duration: 0.5s;
    #top {
      -webkit-animation-name: nohover-bottom-top;
      -webkit-animation-duration: 0.5s;
      animation-name: nohover-bottom-top;
      animation-duration: 0.5s;
    }
  }
  @keyframes top-bottom {
    100% {
      padding-top: 93px;
    }
    75% {
      padding-top: 63px;
    }
    50% {
      padding-top: 33px;
    }
    25% {
      padding-top: 3px;
    }
    0% {
      padding-top: 0;
    }
  }
  @-webkit-keyframes top-bottom {
    100% {
      padding-top: 40px;
    }
    75% {
      padding-top: 30px;
    }
    50% {
      padding-top: 20px;
    }
    25% {
      padding-top: 10px;
    }
    0% {
      padding-top: 0;
    }
  }

  @keyframes bottom-top {
    100% {
      padding-bottom: 0px;
    }
    75% {
      padding-bottom: 63px;
    }
    50% {
      padding-bottom: 103px;
    }
    25% {
      padding-bottom: 143px;
    }
    0% {
      padding-bottom: 183px;
    }
  }
  @-webkit-keyframes bottom-top {
    100% {
      padding-bottom: 0px;
    }
    75% {
      padding-bottom: 63px;
    }
    50% {
      padding-bottom: 103px;
    }
    25% {
      padding-bottom: 143px;
    }
    0% {
      padding-bottom: 183px;
    }
  }
  /********/
  @keyframes nohover-top-bottom {
    0% {
      padding-top: 40px;
    }
    25% {
      padding-top: 30px;
    }
    50% {
      padding-top: 20px;
    }
    75% {
      padding-top: 10px;
    }
    100% {
      padding-top: 0;
    }
  }
  @-webkit-keyframes nohover-top-bottom {
    0% {
      padding-top: 40px;
    }
    25% {
      padding-top: 30px;
    }
    50% {
      padding-top: 20px;
    }
    75% {
      padding-top: 10px;
    }
    100% {
      padding-top: 0;
    }
  }

  @keyframes nohover-bottom-top {
    0% {
      padding-bottom: 23px;
    }
    25% {
      padding-bottom: 63px;
    }
    50% {
      padding-bottom: 103px;
    }
    75% {
      padding-bottom: 143px;
    }
    100% {
      padding-bottom: 183px;
    }
  }
  @-webkit-keyframes nohover-bottom-top {
    0% {
      padding-bottom: 23px;
    }
    25% {
      padding-bottom: 63px;
    }
    50% {
      padding-bottom: 103px;
    }
    75% {
      padding-bottom: 143px;
    }
    100% {
      padding-bottom: 183px;
    }
  }
`;
export default Projectos;
