import React from "react";
import styled from "styled-components";
import Button from "./Button";
import Particles from "react-particles-js";
import { MdArrowDownward } from "react-icons/md";
import { Link } from "react-router-dom";
import Typewriter from "typewriter-effect";
import cv from "./doc/dalciogarcia.pdf";
import { Consumer } from "../Context";

function Casa(props) {
  return (
    <Consumer>
      {({ conteudo }) => {
        const { casa } = conteudo;
        return (
          <Home className="col-12 p-0" id="casa">
            <div className="top">
              <div className="header">
                <div id="wrapper">
                  <span>{"<>"}</span>
                  <span>
                    <Typewriter
                      options={{
                        strings: ["Hello World!", "Olá Mundo!"],
                        autoStart: true,
                        loop: true,
                      }}
                    />
                  </span>
                  <span>{"</>"}</span>
                </div>
                <h2 id="name">Dálcio Garcia</h2>
                <hr />
                <h6>{casa.w}</h6>
                <a href={cv} download>
                  <Button width={"150px"}>{casa.cv}</Button>
                </a>
                <div className="view">
                  <Link to="/eu">
                    <span>
                      <MdArrowDownward />
                    </span>
                    <br />
                    <p>{casa.btn}</p>
                  </Link>
                </div>
              </div>
            </div>
            <div className="particle">
              <Particles
                height="100vh"
                width="100vw"
                params={{
                  particles: {
                    number: {
                      value: 20,
                    },
                    size: {
                      value: 3,
                    },
                  },
                  interactivity: {
                    events: {
                      onhover: {
                        enable: true,
                        mode: "repulse",
                      },
                    },
                  },
                }}
              />
            </div>
          </Home>
        );
      }}
    </Consumer>
  );
}
const Home = styled.div`
  min-height: 100vh;
  width: 100vw;
  color: var(--mainWhite);
  background-color: var(--mainBlack);
  text-align: center;
  overflow: hidden;
  #name {
    font-size: 2rem;
    text-transform: uppercase;
  }
  #wrapper {
    span {
      display: inline-block;
    }
  }
  .top {
    padding-top: calc(50vh - 125px);
    position: absolute;
    z-index: 1;
    width: 100vw;
    height: 100vh;
    background: none;
  }
  .header {
    background: none;
    h6 {
      margin-bottom: 16px;
    }
    hr {
      border-bottom: 1px solid var(--mainGreen);
    }
    @media screen and (min-width: 288px) {
      width: 288px;
    }
    margin-left: auto !important;
    margin-right: auto !important;
    margin-bottom: 10px;
  }
  .particle {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
  }
  .view {
    span {
      font-wight: bold;
      font-size: 25px;
      background-color: var(--mainGreenOpc3);
      padding: 5px 10px 8px 10px;
      border-radius: 50%;
      cursor: pointer;
    }
    p {
      padding-top: 5px;
      color: var(--mainWhiteOpc2);
    }
    :hover {
      span {
        transition: all 0.4s ease;
        a {
          color: var(--mainGreen) !important;
        }
        background-color: var(--mainGreenOpc2);
      }
      p {
        transition: all 0.4s ease;
        color: var(--mainGreenOpc2) !important;
      }
    }
    -webkit-animation: next 1s linear infinite;
    animation: next 1s linear infinite;
  }

  @keyframes next {
    100% {
      margin-top: 20px;
    }
    50% {
      margin-top: 15px;
    }
    0% {
      margin-top: 10px;
    }
  }
  @-webkit-keyframes next {
    100% {
      margin-top: 20px;
    }
    50% {
      margin-top: 15px;
    }
    0% {
      margin-top: 10px;
    }
  }
`;
export default Casa;
