import React from "react";
import styled from "styled-components";
import { Consumer } from "../../Context";
import { MdClose } from "react-icons/md";
import Button from "../Button";

function Modal(props) {
  document.body.style.overflow = "hidden";
  return (
    <Consumer>
      {({
        displayModal,
        setDisplayModal,
        handleModal,
        setHandleModal,
        linguaActiva,
      }) => {
        const { name, info, link, src, tec } = handleModal;
        return (
          <Pano src={src} display={displayModal}>
            <div
              id="click"
              onClick={() => {
                setHandleModal({});
                setDisplayModal("none");
              }}
            ></div>
            <div id="modal">
              <div
                id="close"
                onClick={() => {
                  setHandleModal({});
                  setDisplayModal("none");
                }}
              >
                <MdClose />
              </div>
              <div id="img"></div>
              <div className="col-12" id="info">
                <h3 className="pt-4">{name}</h3>
                <h6 className="">{tec}</h6>
                <hr />
                <p className="">{info[linguaActiva]}</p>
                <a href={link} target="BLANK">
                  <Button cName="col-12">Ver</Button>
                </a>
              </div>
            </div>
          </Pano>
        );
      }}
    </Consumer>
  );
}

const Pano = styled.div`
  display: ${({ display }) => display};
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  bottom: 0;
  #click {
    background-color: rgba(31, 31, 31, 0.9);
    width: 100vw;
    height: 100vh;
  }
  #modal {
    position: absolute;
    -moz-box-shadow: 0px -1px 3px 0.01rem var(--mainWhiteOpc);
    -webkit-box-shadow: 0px -1px 3px 0.01rem var(--mainWhiteOpc);
    box-shadow: 0px -1px 3px 0.01rem var(--mainWhiteOpc);
    background-color: var(--mainBlack);
    width: 576px;
    z-index: 1;
    left: calc(50vw - 288px);
    #close {
      position: absolute;
      z-index: 1;
      top: 5px;
      right: 5px;
      font-size: 30px;
      padding: 0px 16px 0 0;
      color: var(--mainGreen);
      cursor: pointer;
      :hover {
        transition: all 1s ease;
        color: var(--mainGreenOpc2);
      }
    }
    #img {
      width: 100%;
      height: 350px;
      background-color: var(--mainBlack);
      background-image: URL(${({ src }) => src});
      background-size: cover;
    }
  }
  #info {
    h3 {
      color: var(--mainWhite);
    }
    h6 {
      color: var(--mainGreen);
    }
    p {
      color: var(--mainGray);
    }
    hr {
      background-color: var(--mainGray);
    }
  }
  @media screen and (max-width: 576px) {
    #modal {
      width: calc(100vw - 12px);
      left: 6px;
    }
  }
  @media screen and (max-height: 600px) {
    #modal {
      height: calc(100vh - 12px);
      top: 6px;
      #img {
        height: 200px;
      }
    }
  }
  @media screen and (max-height: 456px) {
    #modal {
      height: calc(100vh - 12px);
      top: 6px;
    }
    #img {
      height: calc(100vh - 12px);
    }
    #info {
      background-color: var(--mainBlack);
      position: absolute;
      top: 0;
      padding-top: calc(50vh - 125.5px);
    }
  }
  @media screen and (max-height: 200px) {
    h3 {
      font-size: 24px;
    }
    h6 {
      font-size: 14px;
    }
  }
  @media screen and (max-height: 251px) {
    #img {
      display: none;
    }
    #info {
      p {
        display: none;
      }
    }
  }
  @media screen and (min-width: 576px) {
  }
  @media screen and (min-height: 600px) {
    #modal {
      height: 600px;
      top: calc(50vh - 300px);
    }
  }
`;
export default Modal;
