import React, { useState } from "react";
import { projectos } from "./projectos";
import { conteudo } from "./traducao";
const Context = React.createContext();

const Provider = (props) => {
  const [displayModal, setDisplayModal] = useState("none");
  const [displayLanguage, setDisplayLanguage] = useState("none");
  const [handleModal, setHandleModal] = useState({ ...projectos[0] });
  const [linguaActiva, setLinguaActiva] = useState("pt");

  return (
    <Context.Provider
      value={{
        projectos: projectos,
        displayModal: displayModal,
        setDisplayModal: setDisplayModal,
        handleModal: handleModal,
        setHandleModal: setHandleModal,
        displayLanguage: displayLanguage,
        setDisplayLanguage: setDisplayLanguage,
        linguaActiva: linguaActiva,
        setLinguaActiva: setLinguaActiva,
        conteudo: conteudo[linguaActiva],
      }}
    >
      {props.children}
    </Context.Provider>
  );
};

const Consumer = Context.Consumer;
export { Provider, Consumer };
