import React from "react";
import styled from "styled-components";
import { Box } from "./Box";
import { MdLaptopMac, MdFormatPaint } from "react-icons/md";
import Button from "./Button";
import Foto from "./img/dalcio-260w.jpg";
import cvArq from "./doc/dalciogarcia.pdf";
import { Consumer } from "../Context";
import { Title } from "./Title";

function Side(props) {
  return (
    <Consumer>
      {({ conteudo }) => {
        const { dev, des, frase, cv } = conteudo.me.side;
        const { titulo, sobre } = conteudo.me.sobre;
        return (
          <Lado id="lado">
            <div className="col-12 pt-5 p-0 p-md-4">
              <div className="col-12 d-md-none d-block mt-4 mt-md-0">
                <Title w={"85px"}>{titulo}</Title>
              </div>
              <div className="col-12 pt-4 im p-0">
                <div className="imagem">
                  <img src={Foto} alt="Dálcio Garcia" />
                </div>
              </div>
              <h2 className="col-12 pt-4 pb-4 mt-4 mb-4 m-md-0">
                Dálcio Garcia
              </h2>
              <p className="col-12 p-0 pb-3 p d-md-none d-block ">{sobre}</p>
              <div className="col-12">
                <Box className="col-12 " child="first">
                  <span>
                    <MdLaptopMac />
                  </span>
                  <div>{dev}</div>
                </Box>
                <Box className="col-12">
                  <span>
                    <MdFormatPaint />
                  </span>
                  <div> {des}</div>
                </Box>
              </div>
              <p className="col-12 p-4  mt-4 mb-4 m-md-0 text-align-center d-md-block d-none">
                <i>"{frase}"</i>
              </p>
              <div className="col-12 mt-4 mt-md-0">
                <a href={cvArq} download>
                  <Button cName="col-12 ">{cv}</Button>
                </a>
              </div>
            </div>
          </Lado>
        );
      }}
    </Consumer>
  );
}
const Lado = styled.div`
  padding-bottom: 20px;
  text-align: center;
  overflow-y: scroll;
  h2 {
    color: var(--mainGreen);
  }
  p {
    color: var(--mainGray);
  }
  img {
    height: 200px;
    width: 200px;
    border-radius: 100%;
  }
  @media screen and (width: 768px) {
    overflow-y: scroll !important;
    overflow-x: hidden !important;
    .im {
      padding: 0 !important;
    }
    img {
    }
    .col-12 {
      padding: 16px !important;
    }
  }
  @media screen and (max-width: 767px) {
    overflow-y: hidden !important;
    padding: 16px 64px 0 64px !important;
    img {
      margin-top: 64px;
    }
    .col-12 {
      padding: 0 !important;
    }
  }
  @media screen and (max-width: 412px) {
    padding-left: 0 !important;
    padding-right: 0 !important;
    margin-left: 0 !important;
    margin-top: 0 !important;
    .col-12 {
      width: 100vw !important;
    }
    .col-16 {
      padding-left: 16px !important;
      padding-right: 16px !important;
    }
  }
  background-color: var(--mainBlack);
  @media screen and (min-width: 768px) {
    height: calc(100vh - 6px);
  }
  @media screen and (min-height: 776px) {
    overflow-y: hidden !important;
  }
  &::-webkit-scrollbar {
    max-width: 0.4rem !important;
  }

  &::-webkit-scrollbar-track {
    background: var(--mainGreen) !important;
  }

  &::-webkit-scrollbar-thumb {
    background-color: var(--mainBlack) !important;
  }
  /*firefox*/
  scrollbar-width: thin;
  scrollbar-color: var(--mainBlack) var(--mainGreen);
`;
export default Side;
