import React, { Component } from "react";
import { Title } from "./Title";
import ProgressBar from "./ProgressBar";
import styled from "styled-components";
import { MdDevices, MdTimer, MdLightbulbOutline } from "react-icons/md";
import { Box } from "./Box";
import { Consumer } from "../Context";

class Sobre extends Component {
  render() {
    return (
      <Consumer>
        {({ conteudo }) => {
          const {
            titulo,
            skill,
            sobre,
            responsividade,
            velocidade,
            intuitivo,
          } = conteudo.me.sobre;
          return (
            <Sob className="col-12 pr-5 pl-5 pb-5 pt-md-5 pt-0" id="sobre">
              <span className="d-md-block d-none">
                <Title w={"80px"}>{titulo}</Title>
              </span>
              <div className="col-12 pt-md-5 pt-3">
                <p className="col-12 p-0 pb-3 p d-md-block d-none">{sobre}</p>
                <div className="container p-0">
                  <div className="row">
                    <div className="col-12 col-lg-4 col-xl-3">
                      <div className="row p-3 pt-lg-2">
                        <Box
                          className="col-12 col-md-6 col-lg-12 text-center box first"
                          child="first"
                        >
                          <span>
                            <MdDevices />
                          </span>
                          <div>{responsividade[0]}</div>
                          <p>{responsividade[1]}</p>
                        </Box>
                        <Box
                          className="col-12 col-md-6 col-lg-12 text-center box"
                          child="last"
                        >
                          <span>
                            <MdTimer />
                          </span>
                          <div>{velocidade[0]}</div>
                          <p>{velocidade[1]}</p>
                        </Box>
                        <Box className="col-12  text-center box" child="first">
                          <span>
                            <MdLightbulbOutline />
                          </span>
                          <div>{intuitivo[0]}</div>
                          <p>{intuitivo[1]}</p>
                        </Box>
                      </div>
                    </div>
                    <div className="col-12 col-lg-8 col-xl-9 mt-3 m-md-0">
                      <h4 className="pb-3">{skill}</h4>
                      <ProgressBar title={"HTML5, CSS3"} w={8} />
                      <ProgressBar title={"Javascript"} w={6} />
                      <ProgressBar title={"Node js"} w={5} />
                      <ProgressBar title={"React js"} w={7} />
                      <ProgressBar title={"Next js"} w={4} />
                      <ProgressBar title={"Electron js"} w={6} />
                      <ProgressBar title={"UI Design"} w={5} />
                      <ProgressBar title={"Photoshop"} w={4} />
                    </div>
                  </div>
                </div>
              </div>
            </Sob>
          );
        }}
      </Consumer>
    );
  }
}
const Sob = styled.div`
  .p {
    text-align: center;
  }
  background-color: var(--mainBlack);
  .box1 {
    padding: 0 5px 10px 5px !important;
    span {
      font-size: 24px;
      color: var(--mainGreen);
    }
    :first-child {
      background-color: var(--mainWhiteOpc);
      color: var(--mainBlack);
      p {
        color: var(--mainBlackOpc);
      }
    }
    :last-child {
      border: 1px dotted var(--mainWhite);
    }
  }

  @media screen and (max-width: 412px) {
    padding-left: 0 !important;
    padding-right: 0 !important;
    margin-left: 0 !important;
    margin-top: 0 !important;
    .col-12 {
      width: 100vw !important;
    }
  }
`;
export default Sobre;
