import React from "react";
import styled from "styled-components";
import { Consumer } from "../Context";

function Modal(props) {
  document.body.style.overflow = "hidden";
  return (
    <Consumer>
      {({
        displayLanguage,
        setDisplayLanguage,
        setLinguaActiva,
        linguaActiva,
      }) => {
        const active = linguaActiva === "pt" ? 1 : 2;
        return (
          <Pano display={displayLanguage} active={active}>
            <div
              id="click"
              onClick={() => {
                setDisplayLanguage("none");
              }}
            ></div>
            <ul className="col-12 text-center">
              <li
                onClick={() => {
                  setLinguaActiva("pt");
                }}
              >
                Pt
              </li>
              <li
                onClick={() => {
                  setLinguaActiva("en");
                }}
              >
                En
              </li>
            </ul>
          </Pano>
        );
      }}
    </Consumer>
  );
}

const Pano = styled.div`
  display: ${({ display }) => display};
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  bottom: 0;
  #click {
    background-color: rgba(31, 31, 31, 0.9);
    width: 100vw;
    height: 100vh;
  }
  ul {
    position: absolute;
    top: 0;
    padding: 0 !important;
    top: calc(50vh - 116px);
    left: calc(50vw - 50px);
    height: 216px;
    width: 100px;
    border-radius: 50%;
    @media screen and (min-width: 294px) {
      top: calc(50vh - 50px);
      left: calc(50vw - 108px);
      height: 100px;
      width: 216px;
      li {
        display: inline-block;
        :nth-child(even) {
          margin-left: 16px;
          margin-top: 0;
        }
      }
    }
  }
  li {
    transition: all 0.5s linear;
    color: var(--mainWhiteOpc2);
    height: 100px;
    line-height: 100px;
    display: block;
    width: 100px;
    border-radius: 100%;
    border: thin solid var(--mainWhiteOpc);
    :hover {
      color: var(--mainGreen);
      cursor: pointer;
    }
    background-color: #1f1f1f;
    :nth-child(${({ active }) => active}) {
      border: unset;
      transition: all 0.5s linear;
      background-color: #204641;
      :hover {
        color: var(--mainWhite);
      }
    }
    :nth-child(even) {
      margin-top: 16px;
      margin-left: 0;
    }
  }
`;
export default Modal;
