import React from "react";
import { Title } from "../Title";
import { useState } from "react";
import styled from "styled-components";
import Projectos from "./Projectos";
import { Consumer } from "../../Context";
import Button from "../Button";

function Portfolio(props) {
  const [ulLi, setUlLi] = useState(1);
  const [toShow, setToShow] = useState("all");

  return (
    <Consumer>
      {({ projectos, conteudo }) => {
        const { titulo, todos, btn } = conteudo.me.portfolio;
        return (
          <Container className="col-12 p-5" li={ulLi} id="portfolio">
            <Title w={"82px"}>{titulo}</Title>
            <div className="col-12 pt-5">
              <ul className="col-12 mb-0">
                <li
                  onClick={() => {
                    setUlLi(1);
                    setToShow("all");
                  }}
                >
                  {todos}
                </li>
                {/*
                <li
                  onClick={() => {
                    setUlLi(2);
                    setToShow("mobile");
                  }}
                >
                  Mobile
                </li>*/}
                <li
                  onClick={() => {
                    setUlLi(2);
                    setToShow("web");
                  }}
                >
                  Web
                </li>
                <li
                  onClick={() => {
                    setUlLi(3);
                    setToShow("desktop");
                  }}
                >
                  Desktop
                </li>
              </ul>
              <br />
              <div className="col-12">
                <div className="row">
                  {projectos.map((projecto) => {
                    if (projecto.type === toShow || toShow === "all") {
                      return <Projectos {...projecto} key={projecto.id} />;
                    }
                    return "";
                  })}
                </div>
              </div>
              <br />
              <a href="https://github.com/Dalcio" target="BLANK">
                <Button cName="col-12" width="100%">
                  {btn}
                </Button>
              </a>
            </div>
          </Container>
        );
      }}
    </Consumer>
  );
}

const Container = styled.div`
  ul {
    text-align: center;
    li {
      cursor: pointer;
      display: inline-block;
      padding: 5px 20px;
      :hover {
        color: var(--mainGreen);
      }
      :nth-child(${({ li }) => li}) {
        transition: all 0.5s ease;
        background-color: var(--mainGreen);
        color: var(--mainBlack);
        :hover {
          color: var(--mainBlack);
        }
      }
    }
  }

  @media screen and (max-width: 412px) {
    padding-left: 0 !important;
    padding-right: 0 !important;
    margin-left: 0 !important;
    margin-top: 0 !important;
    .col-12 {
      width: 100vw !important;
    }
  }
`;
export default Portfolio;
