import React from "react";
import styled from "styled-components";
import { MdMenu, MdClear, MdTranslate } from "react-icons/md";
import { HashLink } from "react-router-hash-link";
import { Consumer } from "../Context";

function Menu(props) {
  const { scroll, display, setDisplay } = props;

  return (
    <Consumer>
      {({ setDisplayLanguage, conteudo }) => {
        const { menu } = conteudo;
        return (
          <Header active={scroll} display={display}>
            <div
              className="toggle"
              onClick={() => setDisplay(display === "none" ? "block" : "none")}
            >
              {display === "none" ? (
                <MdMenu />
              ) : (
                <span id="mdclear">
                  <MdClear />
                </span>
              )}
            </div>
            <ul>
              <li
                onClick={() => {
                  setDisplay("none");
                }}
                id="li-1"
              >
                <HashLink to="/"> {menu[0]}</HashLink>
              </li>
              <li
                onClick={() => {
                  setDisplay("none");
                }}
                id="li-2"
              >
                <HashLink to="/eu#sobre"> {menu[1]} </HashLink>
              </li>
              <li
                onClick={() => {
                  setDisplay("none");
                }}
                id="li-3"
              >
                <HashLink to="/eu#portfolio"> {menu[2]}</HashLink>
              </li>
              <li
                onClick={() => {
                  setDisplay("none");
                }}
                id="li-4"
              >
                <HashLink to="/eu#contactos"> {menu[3]} </HashLink>
              </li>
              {
                <li
                  id="translate"
                  onClick={() => {
                    setDisplayLanguage("block");
                  }}
                >
                  <MdTranslate />
                </li>
              }
            </ul>
          </Header>
        );
      }}
    </Consumer>
  );
}
const Header = styled.div`
  background-color: var(--mainBlack);
  border-bottom: 3px solid var(--mainGreen);
  color: var(--mainWhite);
  li {
    display: inline-block;
    padding: 16px 16px 0 16px;
    :hover {
      transition: all 0.5s ease-out;
      a {
        color: var(--mainGreen) !important;
      }
      color: var(--mainGreen) !important;
      cursor: pointer;
    }
  }
  #translate {
    float: right;
  }
  li:nth-child(${({ active }) => active}) {
    a {
      color: var(--mainGreen) !important;
    }
  }
  .toggle {
    display: none;
  }
  @media screen and (width: 768px) {
    li {
      margin-right: 10px;
    }
  }
  @media screen and (max-width: 767px) {
    position: fixed;
    top: 0;
    z-index: 1;
    width: 100%;
    min-height: 59px;
    .toggle {
      font-size: 30px;
      display: block;
      z-index: 1;
      float: right;
      margin-right: 16px;
      font-weight: strong;
      cursor: pointer;
      :hover {
        color: var(--mainGreen) !important;
      }
    }
    ul {
      display: ${({ display }) => display};
      padding: 59px 0 0 0;
      text-align: center;
      li {
        display: block;
      }
      li:nth-child(${({ active }) => active}) {
        color: red !important;
        a {
          color: var(--mainGreen) !important;
        }
      }
      #translate {
        position: absolute;
        z-index: 1;
        top: -2px;
        right: 60px;
      }
    }
  }
`;
export default Menu;
