import React, { useState } from "react";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import Me from "./components/Me";
import Casa from "./components/Casa";
import Side from "./components/Side";
import Menu from "./components/Menu";
import { Switch } from "react-router-dom";
import { Route } from "react-router-dom/cjs/react-router-dom";
import { Consumer } from "./Context";
import Modal from "./components/portfolio/Modal";
import MultiLanguage from "./components/MultiLanguage";
import ReactGA from "react-ga";

function initializeReactGA() {
  ReactGA.initialize("UA-175594615-1");
  ReactGA.pageview("/");
}

function App() {
  initializeReactGA();
  const [scroll, setScroll] = useState(2);
  const [display, setDisplay] = useState("none");
  const [pageW, setPageW] = useState(window.innerWidth);

  window.onresize = () => {
    setPageW(window.innerWidth);
    try {
      if (pageW > 767) {
        let afterasa = document.getElementById("afterCasa");
        let sobre = document.getElementById("sobre").scrollHeight;
        let portfolio =
          document.getElementById("portfolio").scrollHeight + sobre;
        if (scroll === 2) {
          afterasa.scrollTop = 0;
        } else if (scroll === 3) {
          afterasa.scrollTop = sobre;
        } else {
          afterasa.scrollTop = portfolio;
        }
      } else {
        let este = document.getElementById("este");
        let lado = document.getElementById("lado").scrollHeight;
        let sobre = document.getElementById("sobre").scrollHeight + lado;
        let portfolio =
          document.getElementById("portfolio").scrollHeight + sobre;
        if (scroll === 2) {
          este.scrollTop = 0;
        } else if (scroll === 3) {
          este.scrollTop = sobre;
        } else {
          este.scrollTop = portfolio;
        }
      }
    } catch (error) {}
  };
  const handleScroll = () => {
    try {
      let este = document.getElementById("este");
      let lado = document.getElementById("lado").scrollHeight;
      let sobre = document.getElementById("sobre").scrollHeight + lado;
      let portfolio = document.getElementById("portfolio").scrollHeight + sobre;
      if (pageW < 767) {
        const scrollY = este.scrollTop;
        if (scrollY >= 0 && scrollY < sobre) {
          setScroll(2);
        } else if (scrollY >= sobre && scrollY < portfolio) {
          setScroll(3);
        } else if (scrollY >= portfolio) {
          setScroll(4);
        }
      }
    } catch (error) {}
  };
  return (
    <Consumer>
      {({ displayModal, displayLanguage }) => {
        return (
          <div className="container col-2-1">
            <div className="row col-2-1">
              <Switch>
                <Route
                  exact
                  path="/"
                  render={(routeprops) => {
                    return (
                      <>
                        <Casa />
                      </>
                    );
                  }}
                />
                <Route
                  path="/eu"
                  render={(routeprops) => {
                    return (
                      <>
                        <div className="col-12 principal p-0 col-2-1">
                          <div
                            className={`${
                              pageW < 768 ? "este" : "row"
                            } col-2-1 `}
                            id="este"
                            onScroll={handleScroll}
                          >
                            <div className="col-12 col-md-4 col-lg-3 no-p col-2-1s">
                              <Side />
                            </div>
                            <div className="col-12 col-md-8 col-lg-9 no-p col-2-1">
                              <Menu
                                scroll={scroll}
                                display={display}
                                setDisplay={setDisplay}
                              />
                              <Me
                                setScroll={setScroll}
                                routeprops={routeprops}
                                display={display}
                                setDisplay={setDisplay}
                                pageW={pageW}
                              />
                            </div>
                          </div>
                        </div>
                      </>
                    );
                  }}
                />
              </Switch>
              {displayModal === "none" ? "" : <Modal />}
              {displayLanguage === "none" ? "" : <MultiLanguage />}
            </div>
          </div>
        );
      }}
    </Consumer>
  );
}

export default App;
