import styled from "styled-components";

export const Box = styled.div`
  padding: 0 5px 10px 5px !important;
  span {
    font-size: 24px;
    color: var(--mainGreen);
  }
  ${({ child }) =>
    child === "first"
      ? ` 
      {
        background-color: var(--mainWhiteOpc);
        color: var(--mainWhite) !important;
        p {
          color: var(--mainWhiteOpc2) !important;
        } 
      }
    `
      : `
      {
        border: 1px dotted var(--mainWhiteOpc);
        color: var(--mainWhite) !important;
      }
  `}
`;
