import React from "react";
import styled from "styled-components";

function Button(props) {
  const { children, width, onClick, id, cName } = props;
  return (
    <Btn className={"btn " + cName} id={id} w={width} onClick={onClick}>
      {children}
    </Btn>
  );
}

const Btn = styled.button`
  /*width: ${({ w }) => w};*/
  min-height: 38px;
  @media screen and(max-width: ${({ w }) => w} ){
    width: 100vw!important;    
  }
  color: var(--mainGreen) !important;
  a {
    color: var(--mainGreen) !important;
  }
  background-color: transparent;
  border-radius: 0 !important;
  border-color: var(--mainGreen) !important;
  :hover {
    transition: all 0.5s ease-out;
    background-color: var(--mainGreen) !important;
    border-color: var(--mainGreen) !important;
    color: var(--mainBlack) !important;
    a {
      color: var(--mainBlack) !important;
    }
  }
`;
export default Button;
