const { default: styled } = require("styled-components");

export const Title = styled.h2`
  text-align: center;
  color: var(--mainWhite)!important;
  position: relative;
  :after {
    position: absolute;
    content: "";
    bottom: -15px;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    width: ${({ w }) => w};
    height: 3px;
    background-color: var(--mainGreen);
  }
`;
