import React, { Component } from "react";
import { Title } from "./Title";
import styled from "styled-components";
import { MdMail, MdPhone } from "react-icons/md";
import Button from "./Button";
import emailjs from "emailjs-com";
import { Consumer } from "../Context";

class Contactos extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      assunto: "",
      email: "",
      feedback: "",
    };
  }

  inputHandle = (event) => {
    const inputName = event.target.name;
    const value = event.target.value;
    this.setState({ [inputName]: value });
  };

  submit = (event) => {
    event.preventDefault();
    const service_id = "gmail";
    const template_id = "template_nYoPb8KT";
    const user_id = "user_DpSBPprT2v9LK1EZQMobb";

    const { name, assunto, email, feedback } = this.state;
    this.sendFeedback(
      service_id,
      template_id,
      {
        message_html: feedback,
        from_name: name,
        subject: assunto,
        reply_to: email,
      },
      user_id
    );
  };
  sendFeedback = (service_id, template_id, variables, user_id) => {
    emailjs.send(service_id, template_id, variables, user_id).then(
      (result) => {
        //console.log(result.text);
        alert("Email enviado com sucesso.");
      },
      (error) => {
        alert("Email não enviado. Tente denovo");
        /*console.log("falhou o envio******");
        console.log(error.text);
        console.log("********************"); */
      }
    );
  };
  render() {
    return (
      <Consumer>
        {({ conteudo }) => {
          const { titulo, h, form } = conteudo.me.contactos;
          return (
            <Container className="col-12 p-5" id="contactos">
              <Title w={"82px"}>{titulo}</Title>
              <div className="col-12 pt-5">
                <ul className="col-12 text-center p-0">
                  <li id="my-email">
                    <span>
                      <MdMail />
                    </span>
                    dalciomacuetegarcia@gmail.com
                  </li>
                  <li>
                    <span>
                      <MdPhone />
                    </span>
                    +244 935 427 504
                  </li>
                </ul>
                <h4>{h}</h4>
                <form onSubmit={this.submit} className="form col-12 pb-4">
                  <div className="row">
                    <div className="col-12 col-lg-6">
                      <label htmlFor="name">
                        {form[0]} <span>*</span>
                      </label>
                      <input
                        type="text"
                        name="name"
                        className="form-control"
                        id="name"
                        required
                        onChange={this.inputHandle}
                      />
                      <label htmlFor="assunto">{form[1]}</label>
                      <input
                        type="text"
                        name="assunto"
                        className="form-control"
                        id="assunto"
                        onChange={this.inputHandle}
                      />
                      <label htmlFor="email">
                        {form[2]} <span>*</span>
                      </label>
                      <input
                        type="email"
                        name="email"
                        required
                        className="form-control"
                        id="email"
                        onChange={this.inputHandle}
                      />
                    </div>
                    <div className="col-12 col-lg-6">
                      <textarea
                        name="feedback"
                        className="form-control"
                        id="feedback"
                        placeholder={form[3]}
                        cols="30"
                        required
                        onChange={this.inputHandle}
                      />
                    </div>
                  </div>
                  <br />
                  <Button cName="col-12">{form[4]}</Button>
                </form>
              </div>
            </Container>
          );
        }}
      </Consumer>
    );
  }
}
const Container = styled.div`
  background-color: var(--mainBlack);
  min-height: calc(100vh - 66px);
  form {
    background-color: var(--mainGreenOpc) !important;
  }
  li {
    span {
      color: var(--mainGreen);
      margin-right: 16px;
    }
    :last-child {
      margin-top: 16px;
    }
  }
  label {
    margin: 16px 0;
  }
  textarea {
    margin-top: 56px;
    height: 226px;
  }
  @media screen and (max-width: 768px) {
    li {
      span {
        display: block;
      }
    }
  }
  @media screen and (max-width: 992px) {
    textarea {
      margin-top: 16px !important;
    }
  }

  @media screen and (max-width: 258px) {
    #my-email {
      word-break: break-all;
    }
  }
  @media screen and (max-width: 412px) {
    padding-left: 0 !important;
    padding-right: 0 !important;
    margin-left: 0 !important;
    margin-top: 0 !important;
    .col-12 {
      width: 100vw !important;
    }
  }
`;

export default Contactos;
